/* Navbar */
.admin__navbar {
  width: 100%;
  height: 60px;
  position: fixed;
  top: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: row;
          flex-wrap: row;
  padding-top: 10px;
  background-color: whitesmoke;
  z-index: 999;
  transition: all 1s;
}

.list_icon {
  width: 40px;
  height: 40px;
  margin-left: 20px;
  padding: 12px;
      color: black !important;
  transition: all 0.1s;
}

.list_icon:hover {
  background-color: lightgray;
  border-radius: 50%;
  transition: all 0.25s;
}

.list_icon_clicked {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
  transition: all 0.25s;
}

.current__table {
  width: 10%;
  margin-top: 7px;
  margin-left: 30px;
}

.current__table p {
  transition: all 0.25s;
  color:black !important;
}
.current__table p:hover {
  font-weight: bold;
}

.search__form {
  width: 50% !important;
  margin: 0 50px;
}

.search__icon {
  background-color: #212529;
  color: whitesmoke;
  padding: 5px 10px;
  padding-top: 10px;
  cursor: pointer;
  transition: all 0.25s;
  height: 38px;
}

.search__icon:hover {
  background-color: lightgrey;
  color: #212529;
}

.admin__exit{
  position: absolute;
  right:50px;
  text-decoration: none;
  color:black;
  transition:all ease 0.25s;
  margin-top: 5px;
}

.admin__exit:hover{
font-weight: bold;  color:black;
}

/* Slidebar */
.slideBar {
  position: fixed;
  top: 60px;
  width: 15rem;
  height: 100vh;
  background-color: #212529;
  color: whitesmoke;
  transition-timing-function: ease-in;
  transition: all 0.1s;
}

.slideBar:hover {
  width: 18rem;
}

.slideBar_reduced {
  width: 4rem;
}

.slideBar_reduced:hover {
  width: 4rem;
}

.slideBar:hover .title {
  display: block;
}

.slideBar__list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 0;
  margin-top: 50px;
  padding: 0;
}

.slideBar__item {
  display: -webkit-flex;
  display: flex;
  height: 7vh;
  padding-top: 15px;
  cursor: pointer;
}

.slideBar__item:hover {
  background-color: whitesmoke;
  color: #212529 !important;
}

.slideBar__item:hover p{
  color: #212529 !important;
}

.slideBar__item__selected {
  background-color: lightgrey;
  color: #212529 !important;
}

.slideBar__item__selected p{
  color: #212529 !important;
}

.slideBar__title {
  width: 75%;
  margin: 0 auto;
}

.slideBar__title__removed {
  display: none;
}

.slideBar__icon {
  width: 25%;
  margin-left: 20px;
}

/* Table */
.admin__table {
  margin-top: 50px !important;
  margin-left: 20rem !important;
}

.cellule__link {
  cursor: pointer;
  transition: all 0.5s;
}

.cellule__link:hover {
  background-color: lightgrey;
  font-weight: bolder;
}

tbody img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 10px;
}

.cellule__link img:hover {
  transition: all 0.5s;
}

.cellule__link img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

/* Menu */

.menu__bar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  width: 60%;
  margin: 0 auto;
  margin-top: 100px;
  margin-bottom: 20px;
}

.menu__bar * {
  padding: 0 5px;
}

.new__item,
