  }

  tbody img {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-right: 5px;
  }

  /* Pagination */

  .pagination__bar {
    margin-top: -20px;
    margin-left: 60px;
  }

  .pagination__bar p {
    margin-top: 50px;
    font-size: 0.5rem;
  }

  .pagination__bar select {
    margin: 0 10px;
    margin-top: 47px;
    width: 30px !important;
    height: 20px;
    font-size: 0.4rem;
  }
  .page-link {
    width: 15px !important;
    height: 15px !important;
    padding: 2px 4px !important;
    font-size: 0.4rem;
  }

  .start_end_item .page-link {
    width: 25px !important;
    font-size: 0.4rem;
  }

  #popover-basic {
    width: 90px;
    height: 55px;
    font-size: 0.5rem;
  }

  #popover-basic h3 {
    padding: 0.35rem 0.75rem !important;
    font-size: 0.5rem !important;
  }

  #popover-basic input {
    width: 40px;
  }

  #popover-basic button {
    width: 25px;
    font-size: 0.3rem;
  }
}

.skeleton{
  -webkit-animation: skeleton-loading 1s linear  infinite alternate;
          animation: skeleton-loading 1s linear  infinite alternate;
}

@-webkit-keyframes skeleton-loading {
  0%{
    background-color: hsl(200,20%,70%);
  }

    100%{
    background-color: hsl(200,20%,95%);
  }
  
}

@keyframes skeleton-loading {
  0%{
    background-color: hsl(200,20%,70%);
  }

    100%{
